
@import url(https://fonts.googleapis.com/css?family=Khula:700);

.header-home-content {
    height: 80vh;
    width: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: left;
    
    font-family: Khula;
    font-size: 80px;
    line-height: 120px;
    text-align: center;
    padding-bottom: 10vh;

    &__container {
        display: flex;
        flex-direction: row;
        font-weight: 600;
        overflow: hidden;
        height: 120px;
        padding: 0px 99px;

        &__line {
            padding-left: 83px;
            -webkit-animation-name: lineAn;
            -webkit-animation-duration: 10s;
            -webkit-animation-iteration-count: infinite;
            animation-name: lineAn;
            animation-duration: 10s;
            animation-iteration-count: infinite;
            color: #13a5da;

        }
        
        &__vowel {
            &:before {
                content: '>';
                padding-right: 40px;
                left: 0;
            }

            display: flex;
            flex-direction: row;

            @for $i from 0 through 14 {
                $vowelColor: #000000;
                @if $i < 7 {
                    $vowelColor: #000000;
                }
                @else {
                    $vowelColor: #ff9a3c;
                }

                &_#{$i} {
                    color: $vowelColor;

                    -webkit-animation-name: opacity_#{$i};
                    -webkit-animation-duration: 10s;
                    -webkit-animation-iteration-count: infinite;
                    animation-name: opacity_#{$i};
                    animation-duration: 10s;
                    animation-iteration-count: infinite;
                }
            }

            &:after {
                content: '_';
                margin-top: -10px;
                padding-left: 10px;
                -webkit-animation-name: underscore;
                -webkit-animation-duration: 10s;
                -webkit-animation-iteration-count: infinite;
                animation-name: underscore;
                animation-duration: 10s;
                animation-iteration-count: infinite;
            }
        }


    }
}

@media (max-width: 1024px) {
	.header-home-content {
        font-size: 30px;
        line-height: 32px;
    
        &__container {
            height: 32px;
            padding: 0px 29px;
    
            &__line {
                padding-left: 24px;
            }
            
            &__vowel {
                &:before {
                    padding-right: 8px;
                }
        
                &:after {
                    content: '_';
                    margin-top: -5px;
                    padding-left: 5px;
                }

                @for $i from 0 through 14 {
                    $vowelColor: #000000;
                    @if $i < 7 {
                        $vowelColor: #000000;
                    }
                    @else {
                        $vowelColor: #ff9a3c;
                    }
    
                    &_#{$i} {
                        color: $vowelColor;
    
                        -webkit-animation-name: opacity_#{$i};
                        -webkit-animation-duration: 10s;
                        -webkit-animation-iteration-count: infinite;
                        animation-name: opacity_#{$i};
                        animation-duration: 10s;
                        animation-iteration-count: infinite;
                    }
                }
            }
    
    
        }
    }
}


@-webkit-keyframes lineAn {
	0%, 77.99% {opacity:0;}
    80%, 99.99% {opacity:1;}
}

@-o-keyframes lineAn {
    0%, 77.99% {opacity:0;}
    80%, 99.99% {opacity:1;}
}

@-moz-keyframes lineAn {
    0%, 77.99% {opacity:0;}
    80%, 99.99% {opacity:1;}
}

@keyframes lineAn {
    0%, 77.99% {opacity:0;}
    80%, 99.99% {opacity:1;}
}


@for $i from 0 through 14 {
    $endOp: 5 * ($i + 1) - 0.01;
    $startOp: 5 * ($i + 1);
    $targetOp: 0;
    @if $i == 6 {
        $targetOp: 0;
    }
    @else {
        $targetOp: 1;
    }

    @-webkit-keyframes opacity_#{$i} {
        0%, #{$endOp}% {display: none; }
        #{$startOp}%, 99.99% {opacity: $targetOp; }
    }

    @-o-keyframes opacity_#{$i} {
        0%, #{$endOp}%{display: none; }
        #{$startOp}%, 99.99% {opacity: $targetOp; }
    }

    @-moz-keyframes opacity_#{$i} {
        0%, #{$endOp}% {display: none; }
        #{$startOp}%, 99.99% {opacity: $targetOp; }
    }

    @keyframes opacity_#{$i} {
        0%, #{$endOp}% {display: none; }
        #{$startOp}%, 99.99% {opacity: $targetOp; }
    }
}


